* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background: transparent;
}

html {
  font-size: 62.5%;
}
body{
  background-color: black;
}

.box {
  position: relative;
  text-align: center;
  padding: 2rem;
  background-image: linear-gradient(rgba(0, 0, 0, 0.85), rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.83)), url(./images/netflix-background.jpg);
  object-fit: cover;
  font-size: 2rem;
  color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content:center;
  align-items: center;
  .mini-box{
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  h1 {
    font-size: 3.5rem;
    margin-bottom: 1.5rem;
    font-weight: 800;
  }

  h4 {
    font-size: 2rem;
    font-weight: 600;
    margin-bottom: 1.5rem;
  }

  p {
    font-size: 1.5rem;
    margin-bottom: 1.3rem;
  }
}

.form-container {
  position: relative;
  width: 90%;
  background: transparent;

  .form {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    gap: 0.5rem;
  }
  .form-element {
    position: relative;
    width: 30%;
    min-width: 25rem;
  }
}

.form-element input {
  box-sizing: border-box;
  width: 100%;
  font-family: inherit;
  padding: 1rem 0.5rem 0rem 1rem;
  height: 5rem;
  font-size: 1.5rem;
  border: 1px solid gray;
  border-radius: 3px;
  background-color: #131212a8;
  color: #ffffff;
}

input:focus {
  outline: 1.5px solid white;
}

input:valid {
  background-color: rgba(0, 0, 0, 0.433);
}

.floating-label {
  background-color: transparent;
  box-sizing: border-box;
  width: fit-content;
  position: absolute;
  top: 25%;
  left: 1rem;
  font-size: 1.6rem;
  cursor: text;
  color: #b4b4b4;
  transition: font 0.1s ease, top 0.1s ease, transform 0.1s ease;
  transition: font 0.1s ease, top 0.1s ease, transform 0.1s ease,
    -webkit-transform 0.1s ease, -moz-transform 0.1s ease,
    -o-transform 0.1s ease;
}

.bttn {
  width: 150px;
  height: 5rem;
  cursor: pointer;
  font-size: 1.8rem;
  font-weight: 700;
  border-radius: 3.5px;
  margin-left: 0.6rem;
  border: none;
  color: #ffffff;
  background-color: #e50914;

  &:hover {
    background-color: #c40811 !important;
  }
}

input:focus~.floating-label,
input:valid~.floating-label {
  font-size: 1.1rem;
  top: 0.3rem;
}

.banner:nth-child(even) {
  .content {
    flex-direction: row-reverse;
  }
}

@media screen and (max-width:768px) and (max-height:700px) {
  .box {
    height: 130vh;
    h1 {
      font-size: 3rem;
      font-weight: 800;
    }

    h4 {
      font-size: 1.4rem;
    }

    p {
      font-size: 2rem;
    }
  }
}