.watch{
    margin: 0;padding: 0;
    width: 100%;
    height: 99vh;
    position: relative;
    .back{
        position: absolute;
        color: white;
        top: 1rem;
        left: 1rem;
        font-size: 1.3rem;
        z-index: 3;
    }
    .video{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}